.todayspopupbutton {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  padding: 10px 15px;
  border: 3px solid #a69e06;
  border-radius: 13px;
  background: #ede209;
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(#ede209),
    to(#a69e06)
  );
  background: -moz-linear-gradient(top, #ede209, #a69e06);
  background: linear-gradient(to bottom, #ede209, #a69e06);
  -webkit-box-shadow: #ffff0b 0px 0px 40px 0px;
  -moz-box-shadow: #ffff0b 0px 0px 40px 0px;
  box-shadow: #ffff0b 0px 0px 40px 0px;
  text-shadow: #ffff0c 1px 1px 1px;
  font: normal normal bold 23px trebuchet ms;
  color: #111111;
  text-decoration: none;
}
.todayspopupbutton:hover,
.todayspopupbutton:focus {
  border: 3px solid #ede209;
  background: #ffff0b;
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(#ffff0b),
    to(#c7be07)
  );
  background: -moz-linear-gradient(top, #ffff0b, #c7be07);
  background: linear-gradient(to bottom, #ffff0b, #c7be07);
  color: #111111;
  text-decoration: none;
}
.todayspopupbutton:active {
  background: #a69e06;
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(#a69e06),
    to(#a69e06)
  );
  background: -moz-linear-gradient(top, #a69e06, #a69e06);
  background: linear-gradient(to bottom, #a69e06, #a69e06);
}
