/* Used to fade in a section  */
.fade-in-section {
  width: 100%;
  opacity: 0;
  transform: translateY(20vh);
  visibility: hidden;
  transition: opacity 0.6s ease-out, transform 1.2s ease-out;
  will-change: opacity, visibility;
}
.fade-in-section.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
}

/* Add a blue shadow border around buttons */
.blueborderbutton {
  border-width: 2px;
  border-image: radial-gradient(
      100% 185.14% at 100% 100%,
      rgb(52, 195, 255) 0%,
      rgb(51, 142, 238) 100%
    )
    5 / 2px / 0 stretch;
}

.blueborderbutton:hover {
  transition: box-shadow 0.25s cubic-bezier(0, 0.42, 0.2, 0.99) 0s,
    margin 0.25s cubic-bezier(0, 0.42, 0.2, 0.99) 0s;
}

